@import "vars";
@import "fonts";
@import "header";
@import "buttons";
@import "categories";
@import "cart";
@import "inputs";
@import "swiper-slider-custom";
@import "footer";

::-moz-selection {
	background-color: $accent;
	color: #fff;
}
::selection {
	background-color: $accent;
	color: #fff;
}
* {
	margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html, body {
	height: 100%;
}
body {
	font-size: 16px;
	min-width: 320px;
	position: relative;
	font-family: $default;
	color: $text;
	overflow-x: hidden;
	display: flex;
    flex-direction: column;
    background-color: $bg;
}

img, svg {
	max-height: 100%;
	max-width: 100%;
	width: auto;
	height: auto;
}
input, button, textarea {
	outline: none;
	font-family: $default;
	&:focus {
		outline: none;
	}
}
input, textarea {
	padding: 10px;
}
textarea {
	resize: none;
}
ul {
	list-style: none;
}
a {
	text-decoration: none;
	color: inherit;
	@extend.animate;
	&:hover {
		text-decoration: none;
		color: inherit;
	}
}
h1, h2, h3, h4 {
	font-weight: inherit;
}
button {
	color: #fff;
	border: 1px solid currentColor;
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 5px;
	font-weight: bold;
	@extend.animate;
	&:hover {
		color: #ffffff;
	}
}
.animate {
	transition: all 0.3s ease;
}
.no_scroll {
	overflow: hidden;
	&:after {
		content: '';
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background: rgba(0, 0, 0, 0.6);
	}
}

////main content

.main {
  margin-top: 80px;
  margin-bottom: 40px;
  padding-top: 20px;
  padding-right: calc(0px + 350px);
  padding-left: calc(0px + 75px);
  @media only screen and (max-width: 1024px) {
    padding: 20px;
    padding-bottom: 80px;
  }
  .inner-content {
    padding: 30px;
  }
}

.small {
  font-size: 12px;
}

.roboto {
   font-family: $roboto;
}

.accent-text {
  color: $accent;
}

.swiper-container {
  width: 100%;
  height: 450px;
}

@import "media"; /* Always at the end*/
